import React from "react";
import { View, Text, PanResponder } from "react-native";
import { ProcessNodeDisplay } from "./process.node.display";
import Svg, { Line } from 'react-native-svg';

export const ProcessEditor = ({ navigation }) => {
    const [position, setState] = React.useState({ x: 200, y: 200 });
    const lastPosition = React.useRef({ x: 200, y: 200 }).current;
    let pressCount = 0;
    const panResponder = React.useMemo(() => PanResponder.create({
        onStartShouldSetPanResponder: (evt, gestureState) => true,
        onPanResponderGrant: (evt, gestureState) => {
            pressCount += 1;
            setTimeout(() => {
                pressCount = 0;
            }, 500)
        },
        onPanResponderMove: (evt, gestureState) => {
            setState({ x: lastPosition.x + gestureState.dx, y: lastPosition.y +  gestureState.dy });
        },
        onPanResponderRelease: (evt, gestureState) => {
            lastPosition.x += gestureState.dx;
            lastPosition.y += gestureState.dy;
            if (pressCount === 2) {
                navigation.navigate({
                    routeName: 'Modal'
                });
            }
        },
        
    }), []);

    return (
        <View style={{ flex: 1, flexDirection: "row", overflow: "hidden", backgroundColor: "#999999" }} {...panResponder.panHandlers} > 
            <View style={{ flex: 1, zIndex: -1, transform: ([ { translateX: position.x }, { translateY: position.y }]) }} >
                <ProcessNodeDisplay {...{ id: 1, type: 0, position: { x: 0, y: 0 } }} />
                <ProcessNodeDisplay {...{ id: 2, type: 0, position: { x: 100, y: 100 } }} />
                <Svg height="100%" width="100%">
                    <Line x1="60" y1="30" x2="100" y2="130" stroke="red" strokeWidth="1" />
                </Svg>
            </View>   
        </View>
    );
}